.messageWithAvatarContainer {
    display: flex;
    width: 300px;
    background: chartreuse;
    padding: 10px;
    margin: 10px;
    align-items: flex-end;
}

.avatar {
    height: 50px;
    padding-right: 5px;
    border-radius: 50%;
}

.messageFlexCont {
    display: flex;
    flex-wrap: wrap;
    background: bisque;
    min-width: 200px;
    padding: 10px;
    position: relative;
    border-radius: 0.5em;


}

.messageFlexCont div {
    width: 100%;
}

.userName {
    font-weight: bolder;
    color: darkslateblue;
    padding-bottom: 2px;
}


.time {
    opacity: 0.5;
    position: absolute;
    right: 10px;
    bottom: 5px;


}